



















































import Swal from 'sweetalert2';
import Jsbarcode from 'jsbarcode';
import { State, Action, Getter } from 'vuex-class';
import {
  IArticle, IListInput, IExamInfoListInput, ICommercialItem, INotification,
} from '@/types/interface';
import { Component, Vue } from 'vue-property-decorator';
import MainFooter from '../../components/MainFooter.vue';
import BaseCarousel from '../../components/BaseCarousel.vue';
import BaseArticleList from '../../components/BaseArticleList.vue';
import AdvertisingBannerList from '../../components/AdvertisingBannerList.vue';

@Component({
  components: {
    MainFooter,
    BaseCarousel,
    BaseArticleList,
    AdvertisingBannerList,
  },
})

export default class MainPage extends Vue {
  @State('memberId') stateMemberId !: string;
  @State('commercialList') stateCommercialList!: ICommercialItem[];
  @State('articleList', { namespace: 'article' }) stateArticleList!: IArticle[];
  @State('examInfoList', { namespace: 'examInfo' }) stateExamInfoList!: IArticle[];
  @State('notificationList', { namespace: 'notification' }) stateNotificationList!: INotification[];
  @Getter('topCommercial') topCommercial!: ICommercialItem[];
  @Action('getExamInfoList', { namespace: 'examInfo' }) getExamInfoList!: (params: IExamInfoListInput) => void;
  @Action('getExamInfoSimple', { namespace: 'examInfo' }) getExamInfoSimple!: (id: number) => Promise<void>;
  @Action('getArticleList', { namespace: 'article' }) getArticleList!: (params: IListInput) => void;
  @Action('getArticleSimple', { namespace: 'article' }) getArticleSimple!: (id: number) => Promise<void>;
  @Action('getCommercialList') getCommercialList!: (id?: string) => void;
  @Action('getNotificationList', { namespace: 'notification' }) getNotificationList!: () => Promise<void>;
  // imgList = ['@/assets/test/test1.png', '../../assets/test/test1.png', '../../assets/test/test1.png'];

  get newNotify() {
    return this.stateNotificationList.find((d: INotification) => d.unread);
  }

  created() {
    this.getExamInfoList({
      searchKey: '',
      keyword: '',
      isLongPeriod: false,
    });

    this.getArticleList({
      count: '4',
      // eslint-disable-next-line @typescript-eslint/camelcase
      search_key: '',
      keyword: '',
      days: '1000',
      // eslint-disable-next-line @typescript-eslint/camelcase
      at2type_id: '',
    });

    this.getCommercialList('top');
    this.getCommercialList();
    this.getNotificationList();
  }

  linkAssociation() {
    this.$router.push('/main/information/selection');
  }

  linkContent() {
    this.$router.push('/main/information/content');
  }

  linkExamInfo() {
    this.$router.push('/main/exam_info');
  }

  linkExamInfoContent(id: number) {
    this.getExamInfoSimple(id)
      .then(() => {
        this.$router.push({ path: '/main/exam_info_content', query: { id: id.toString() } });
      });
  }

  linkArticle() {
    this.$router.push('/main/article');
  }

  linkArticleContent(id: number) {
    this.getArticleSimple(id)
      .then(() => {
        this.$router.push({ path: '/main/article_content', query: { id: id.toString() } });
      });
  }

  showNotification() {
    this.$router.push('/notification/main');
  }

  ticketCenter() {
    this.$router.push('/ticket/main');
  }

  showBarcode() {
    Swal.fire({
      width: 312,
      showCloseButton: true,
      showConfirmButton: false,
      background: '#FFFFFF',
      title: '會員條碼',
      html: '<canvas id="barcode"></canvas>',
      customClass: {
        popup: 'modal-barcode-popup',
        title: 'modal-barcode-title',
        content: 'modal-barcode-content',
        closeButton: 'modal-barcode-close',
      },
    });

    Jsbarcode('#barcode', this.stateMemberId, {
      width: 2,
      height: 50,
      font: 'Noto Sans CJK TC',
      fontSize: 16,
    });
  }
}
