
























import {
  Component, Vue, Prop, Emit,
} from 'vue-property-decorator';
import { IArticle } from '@/types/interface';

@Component
export default class BaseArticleList extends Vue {
  @Prop({ default: () => [] }) list!: IArticle[];

  @Emit()
  checkMore() {
    return '';
  }

  @Emit()
  checkOne(id: number) {
    return id;
  }
}
