















import { Component, Prop, Vue } from 'vue-property-decorator';
import { ICommercialItem } from '@/types/interface';

@Component
export default class BaseCarousel extends Vue {
  @Prop({ default: () => [] }) list!: ICommercialItem[];

  flag = 0;
  // length = 3;
  carouselName = 'carousel-next';
  toggleTimer = true;
  timerDelay = 3000;
  timer = 0;

  get filterItem() {
    return this.list[this.flag];
  }

  get length() {
    return this.list.length;
  }

  changeFlag(idx: number) {
    this.toggleTimer = false;
    clearInterval(this.timer);
    this.flag = idx;
  }

  toNext() {
    if (this.flag === (this.length - 1)) this.flag = 0;
    else this.flag += 1;
  }

  autoPlay() {
    this.timer = setInterval(() => {
      if (this.toggleTimer) this.toNext();
    }, this.timerDelay);
  }

  mounted() {
    this.autoPlay();
  }

  link(url: string) {
    window.open(url);
  }

  beforeDestroy() {
    clearInterval(this.timer);
  }
}
